export default "@import '../../themes/src/default.css';\n\n:root {\n    /* primary */\n    --link-primary-color: var(--color-light-text-primary);\n    --link-primary-hover-color: var(--color-light-text-primary-shade-20);\n    --link-primary-active-color: var(--color-light-text-primary-shade-40);\n\n    /* secondary */\n    --link-secondary-color: var(--color-light-text-secondary);\n    --link-secondary-hover-color: var(--color-light-text-secondary-shade-20);\n    --link-secondary-active-color: var(--color-light-text-secondary-shade-40);\n\n    /* default */\n    --link-default-color: var(--color-light-text-link);\n    --link-default-hover-color: var(--color-light-text-link-shade-20);\n    --link-default-active-color: var(--color-light-text-link-shade-40);\n}\n\n.primary {\n    color: var(--link-primary-color);\n    border-bottom-color: var(--link-primary-color);\n\n    &:hover {\n        color: var(--link-primary-hover-color);\n    }\n\n    &:active {\n        color: var(--link-primary-active-color);\n    }\n}\n\n.secondary {\n    color: var(--link-secondary-color);\n    border-bottom-color: var(--link-secondary-color);\n\n    &:hover {\n        color: var(--link-secondary-hover-color);\n    }\n\n    &:active {\n        color: var(--link-secondary-active-color);\n    }\n}\n\n.defaultView {\n    color: var(--link-default-color);\n    border-bottom-color: var(--link-default-color);\n\n    &:hover {\n        color: var(--link-default-hover-color);\n    }\n\n    &:active {\n        color: var(--link-default-active-color);\n    }\n}\n";