export default "@import '../../themes/src/default.css';\n\n:root {\n    /* primary */\n    --link-inverted-primary-color: var(--color-light-text-primary-inverted);\n    --link-inverted-primary-hover-color: color-mod(var(--link-inverted-primary-color) shade(20%));\n    --link-inverted-primary-active-color: color-mod(var(--link-inverted-primary-color) shade(40%));\n\n    /* secondary */\n    --link-inverted-secondary-color: var(--color-light-text-secondary-inverted);\n    --link-inverted-secondary-hover-color: color-mod(\n        var(--link-inverted-secondary-color) shade(20%)\n    );\n    --link-inverted-secondary-active-color: color-mod(\n        var(--link-inverted-secondary-color) shade(40%)\n    );\n\n    /* default */\n    --link-inverted-default-color: var(--color-light-text-link);\n    --link-inverted-default-hover-color: color-mod(var(--link-inverted-default-color) shade(20%));\n    --link-inverted-default-active-color: color-mod(var(--link-inverted-default-color) shade(40%));\n}\n\n.primary {\n    color: var(--link-inverted-primary-color);\n    border-bottom-color: var(--link-inverted-primary-color);\n\n    &:hover {\n        color: var(--link-inverted-primary-hover-color);\n    }\n\n    &:active {\n        color: var(--link-inverted-primary-active-color);\n    }\n}\n\n.secondary {\n    color: var(--link-inverted-secondary-color);\n    border-bottom-color: var(--link-inverted-secondary-color);\n\n    &:hover {\n        color: var(--link-inverted-secondary-hover-color);\n    }\n\n    &:active {\n        color: var(--link-inverted-secondary-active-color);\n    }\n}\n\n.defaultView {\n    color: var(--link-inverted-default-color);\n    border-bottom-color: var(--link-inverted-default-color);\n\n    &:hover {\n        color: var(--link-inverted-default-hover-color);\n    }\n\n    &:active {\n        color: var(--link-inverted-default-active-color);\n    }\n}\n";