export default "@import '../../themes/src/default.css';\n\n.component {\n    box-sizing: border-box;\n    text-decoration: none;\n    font-family: var(--font-family);\n    cursor: pointer;\n    outline: none;\n\n    &:hover .text,\n    &:active .text {\n        border-bottom-color: rgba(0, 0, 0, 0);\n    }\n}\n\n.withAddons {\n    display: inline-flex;\n    flex-direction: row;\n    flex-wrap: nowrap;\n}\n\n.text {\n    border-bottom: 1px solid;\n    transition: border 0.2s ease, color 0.2s ease;\n}\n\n.focused {\n    @mixin focus-outline;\n}\n\n.pseudo {\n    margin: 0;\n    padding: 0;\n    background-color: transparent;\n    border: none;\n    box-shadow: none;\n    font: inherit;\n}\n\n.pseudo .text {\n    border-bottom-style: dashed;\n}\n\n.withoutUnderline .text {\n    border-bottom: none;\n}\n\n.addons {\n    display: flex;\n    align-items: center;\n    transition: color 0.2s ease;\n}\n\n.addons:first-child {\n    margin-right: var(--gap-xs);\n}\n\n.addons:last-child {\n    margin-left: var(--gap-2xs);\n}\n\n.addons:only-child {\n    margin-right: 0;\n    margin-left: 0;\n}\n";